@import '../../../scss/abstracts/all';

:local(.listItem) {
	line-height: 1.6;
	border-bottom: 1px solid $primary-border;
	-webkit-box-align: center;
	-webkit-align-items: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-pack: center;
	-webkit-justify-content: center;
	-ms-flex-pack: center;
	justify-content: center;
	&:first-of-type {
		border-top: 1px solid $primary-border;	
	}

	:local(.listItemTitle) {
		display: block;
		font-size: 1.2em;		
		margin-top: 10px;
		
	}

	:local(.listItemInfo) {
		display: block;
		font-size: 1em;
		margin: 10px 0;
	}

	:local(.listItemType) {
		display: block;
		font-size: .8em;		
	}
	:local(.listItemDate) {
		font-size: .8em;
	}
	:local(.icon) {
		margin-left: 10px;
		font-size: 1.2em;
		color: #333;
	}
}
